import { useStore } from "vuex";
import { reactive, ref, watch, onMounted, onBeforeMount } from "vue";
import { useRouter, useRoute } from "vue-router"; // 引入ts接口

import { animationUpDown } from "./types/layout";
import { ElMessage } from "element-plus";
import MyDate from "@/utils/date";
export default {
  name: "app",

  setup() {
    document.title = "loading..."; // 获取的路由对象

    const Router = useRouter(),
          Route = useRoute();
    let loading = ref(true); // 页面设置相关

    let pageSetting = reactive({
      // 设置当前展示页
      activeIndex: 1,
      // 设置头部header显示与隐藏
      isHeader: false,
      // 控制滚轮的节流阀
      settimer: true,
      // 动画上下初始控制
      AnimationUpDown: animationUpDown.fade
    }); // 使用store

    const store = useStore();
    let music = reactive([]);
    let currentMusic = reactive({
      id: -1,
      playUrl: "",
      name: "",
      imageUrl: "",
      singer: ""
    });
    let contorlIsShow = ref(false);
    let mIndex = ref(0);
    let audio = ref();
    let fil = ref();
    let pausedState = ref(true);
    let LoadingState = ref(false);
    let fristLoad = 0;
    let timeObj = reactive({
      currentTime: "00:00",
      duration: "00:00"
    }); // 对象数组拷贝

    music = store.state.musicInfoLists.map(o => Object.assign({}, o)); // 监视路由的变化

    watch(() => Route.meta, newValue => {
      // 控制页面title展示
      document.title = newValue.title; //  控制顶部header和malk展示

      pageSetting.isHeader = newValue.index != 1 && newValue.index != 8 ? true : false; // 统一index

      pageSetting.activeIndex = newValue.index;
    }, {
      immediate: true
    });
    watch(mIndex, newV => {
      Object.assign(currentMusic, music[newV]);
    }, {
      immediate: true
    }); // 监视路由的变化

    onBeforeMount(() => {
      if (document.readyState == "loading") {
        loading.value = true;
      }
    });
    onMounted(() => {
      let timer = setInterval(() => {
        if (document.readyState == "complete") {
          loading.value = false;
          clearInterval(timer);
        }
      }, 1000);

      document.body.onkeydown = function (event) {
        event.preventDefault();
      };
    }); // 鼠标滚动事件

    function wheelCheckout(e) {
      if (pageSetting.settimer) {
        // 开启节流
        pageSetting.settimer = false;

        if (e.deltaY > 0) {
          pageSetting.activeIndex = pageSetting.activeIndex >= Router.options.routes.length - 2 ? Router.options.routes.length - 2 : pageSetting.activeIndex + 1;
          pageSetting.AnimationUpDown = animationUpDown.down;
        } else {
          pageSetting.activeIndex = pageSetting.activeIndex <= 1 ? 1 : pageSetting.activeIndex - 1;
          pageSetting.AnimationUpDown = animationUpDown.up;
        } // 跳转至对应页面


        routerJump(Router.options.routes); // 关闭节流阀

        setTimeout(() => {
          pageSetting.settimer = true;
        }, 1500);
      }
    } // 路由跳转方法


    function routerJump(routes) {
      routes.forEach(item => {
        var _item$meta;

        if (((_item$meta = item.meta) === null || _item$meta === void 0 ? void 0 : _item$meta.index) == pageSetting.activeIndex) {
          Router.push(item.path);
        }
      });
    }

    function onPush(item) {
      Router.push({
        path: item.path
      });
    }

    let routes = Router.options.routes.slice(0, -2);

    const enter = () => {
      contorlIsShow.value = true;
    };

    const leave = () => {
      contorlIsShow.value = false;
    };

    const canplay = () => {
      // 更改为不是加载状态
      LoadingState.value = false;
      pausedState.value = true;

      if (fristLoad != 0) {
        toPlay();
      }

      fristLoad++;
    };

    const toPlay = () => {
      if (!LoadingState.value) {
        audio.value.play();
        pausedState.value = false;
      }
    };

    const nextEvent = () => {
      LoadingState.value = true;
      mIndex.value++;

      if (mIndex.value >= music.length) {
        mIndex.value = 0;
      }
    };

    const preEvent = () => {
      LoadingState.value = true;

      if (mIndex.value > 0) {
        mIndex.value--;
      } else {
        mIndex.value = music.length - 1;
      }
    };

    const PlayEvent = () => {
      if (audio.value.paused) {
        toPlay();
        pausedState.value = false;
      } else {
        audio.value.pause();
        pausedState.value = true;
      }
    };

    const pause = () => {
      fil.value.style.animationPlayState = "paused";
    };

    const playChange = () => {
      fil.value.style.animationName = "rotate";
      fil.value.style.animationPlayState = "running";
    };

    const about = () => {
      fil.value.style.animationName = "";
      fil.value.style.animationPlayState = "";
      pausedState.value = true;
    };

    const error = () => {
      ElMessage({
        type: "error",
        message: "播放错误"
      });
      pausedState.value = true;
    };

    const ended = () => {
      pausedState.value = true;
      fil.value.style.animationName = "";
      fil.value.style.animationPlayState = "";
    };

    const stalled = () => {
      ElMessage({
        type: "error",
        message: "音乐不可用"
      });
      pausedState.value = true;
    };

    const timeupdate = () => {
      let duration = MyDate.withMilliseconds(Math.floor(audio.value.duration));
      let currentTime = MyDate.withMilliseconds(Math.floor(audio.value.currentTime));
      Object.assign(timeObj, {
        duration,
        currentTime
      });
    };

    return {
      pageSetting,
      wheelCheckout,
      routes,
      contorlIsShow,
      enter,
      leave,
      onPush,
      nextEvent,
      preEvent,
      PlayEvent,
      error,
      about,
      ended,
      stalled,
      canplay,
      pause,
      playChange,
      timeupdate,
      loading,
      timeObj,
      pausedState,
      audio,
      fil,
      currentMusic
    };
  }

};